@keyframes scroll-top {
  0% {
    -webkit-transform: translateZ(0);
    -webkit-transform: translateY(0%);
    transform: translateY(0%); }
  100% {
    -webkit-transform: translateZ(0);
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%); } }

.animation-parent {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  position: relative; }
  .animation-parent .one {
    position: absolute;
    top: 10%; }
    .animation-parent .one.align-bottom {
      top: 100%; }
    .animation-parent .one.hide {
      display: none; }
    .animation-parent .one .end.hide {
      display: none; }
  .animation-parent .two {
    position: absolute;
    top: 10%; }
    .animation-parent .two.align-bottom {
      top: 100%; }
    .animation-parent .two.hide {
      display: none; }
    .animation-parent .two .end.hide {
      display: none; }
