@import url(../polling/fontello.css);
@import url("https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap");
@keyframes scroll-up {
  0% {
    transform: translateY(0%); }
  100% {
    transform: translateY(-100%); } }

body {
  margin: 0; }

.container {
  margin-top: 100px;
  padding-left: 12px;
  padding-right: 12px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: #050608;
  font-family: CiscoSansThin; }
  .container .header {
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    padding-bottom: 30px;
    padding-top: 10px;
    z-index: 10; }
    .container .header .header-logo {
      align-self: flex-end;
      width: 110px;
      height: 60px; }
    .container .header .heading {
      color: #1dbbe9; }
  .container .top {
    width: 100%;
    margin-bottom: 30px;
    margin-right: 12px; }
    .container .top .post:nth-child(1) .post-body {
      position: relative; }
      .container .top .post:nth-child(1) .post-body .medal {
        position: absolute;
        top: 0px;
        right: 5px;
        display: block;
        background: url(/assets/medals/gold.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 30px;
        height: 40px; }
    .container .top .post:nth-child(2) .post-body {
      position: relative; }
      .container .top .post:nth-child(2) .post-body .medal {
        position: absolute;
        top: 0px;
        right: 5px;
        display: block;
        background: url(/assets/medals/silver.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 30px;
        height: 40px; }
    .container .top .post:nth-child(3) .post-body {
      position: relative; }
      .container .top .post:nth-child(3) .post-body .medal {
        position: absolute;
        top: 0px;
        right: 5px;
        display: block;
        background: url(/assets/medals/bronze.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 30px;
        height: 40px; }
  .container .rest-parent {
    overflow: hidden;
    width: 100%;
    padding-right: 12px;
    height: 500px; }
  .container .rest {
    width: 100%;
    animation: scroll-up 90s linear 5s infinite; }
    .container .rest .post .post-img img {
      min-width: 77px;
      margin: 10px;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      max-width: 75px;
      height: 75%;
      max-height: 75px;
      border: solid 1px #cecece;
      background-color: #004e7c; }
    .container .rest .post .post-body {
      justify-content: space-evenly;
      position: relative; }
      .container .rest .post .post-body .post-content .rank {
        display: block;
        position: absolute;
        top: 10px;
        right: 0;
        color: #000000;
        background-color: #00bceb;
        width: 30px;
        height: 15px;
        border-radius: 7.5px;
        text-align: center;
        font-size: 1.5em; }
      .container .rest .post .post-body .post-footer {
        margin-top: 20px; }
      .container .rest .post .post-body .post-content .author-name {
        margin-top: 1px; }
  .container .post {
    border-radius: 10px;
    background-color: #ffffff;
    height: 110px;
    width: 98%;
    margin: 10px 10px 10px 10px;
    display: flex; }
    .container .post .post-img {
      display: flex;
      justify-content: center;
      align-items: center; }
      .container .post .post-img img {
        max-width: 110px;
        height: 100%;
        max-height: 110px;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        background-color: #f4f4f4; }
    .container .post .post-body {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start;
      margin-left: 12px;
      margin-right: 12px;
      width: 100%;
      word-wrap: break-word; }
      .container .post .post-body .rank {
        display: none; }
      .container .post .post-body .medal {
        display: none; }
      .container .post .post-body .post-content .author-name {
        white-space: nowrap;
        width: 120px;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 6px;
        font-size: 2em;
        letter-spacing: 0.6px; }
      .container .post .post-body .post-content .author-handle {
        font-size: 1.8em;
        color: #004e7c;
        letter-spacing: 0.5px; }
      .container .post .post-body .post-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        border-top: 1px solid #a6a6a6;
        font-family: Roboto;
        font-weight: 300; }
        .container .post .post-body .post-footer .post-count {
          font-size: 1.8em; }
          .container .post .post-body .post-footer .post-count span {
            font-size: 2em; }
        .container .post .post-body .post-footer .post-logo {
          align-self: flex-end;
          margin-right: 10px;
          margin-top: 10px; }
          .container .post .post-body .post-footer .post-logo .icon-twitter {
            color: #3aaae1;
            font-size: 2.5em; }
          .container .post .post-body .post-footer .post-logo img {
            width: 20px; }
