@keyframes scroll {
  0% {
    transform: translate(0, 0); }
  99% {
    transform: translate(0, calc(-100% + 76vh)); }
  100% {
    transform: translate(0, 0); } }

.leaderboard.tile {
  font-family: CiscoSans;
  border-radius: 7px;
  background-color: #ffffff;
  padding: 1vw;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden; }
  .leaderboard.tile .body {
    overflow: hidden; }
    .leaderboard.tile .body .contents {
      animation: scroll 45s linear infinite;
      animation-delay: 5s; }
  .leaderboard.tile .leader-row {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    border-radius: 5px;
    background-color: #fafafa;
    box-shadow: 0.6px 0.8px 3px 0 rgba(0, 0, 0, 0.35);
    border: solid 1px rgba(121, 121, 121, 0.27);
    padding: 5px;
    margin: 10px 0; }
    .leaderboard.tile .leader-row .avatar {
      width: 70px;
      height: 81px;
      padding: 10px;
      border: solid 1px rgba(121, 121, 121, 0.27); }
      .leaderboard.tile .leader-row .avatar img {
        width: 70px;
        height: 81px; }
    .leaderboard.tile .leader-row .info {
      flex-grow: 3;
      align-self: center;
      margin-left: 10px; }
      .leaderboard.tile .leader-row .info .name {
        font-size: 24px;
        line-height: 1.5;
        letter-spacing: 0.5px;
        text-align: left; }
      .leaderboard.tile .leader-row .info .handle {
        font-size: 19px;
        line-height: 0.95;
        letter-spacing: 0.4px;
        text-align: left;
        color: #005073; }
    .leaderboard.tile .leader-row .social {
      align-self: center;
      margin: 20px;
      width: 15%; }
      .leaderboard.tile .leader-row .social img {
        width: 35px;
        flex-grow: 2; }
      .leaderboard.tile .leader-row .social .posts-count {
        position: absolute;
        right: 15px;
        bottom: 10px;
        min-width: 100px;
        font-size: 20px; }
        .leaderboard.tile .leader-row .social .posts-count img {
          vertical-align: text-bottom;
          width: 30px;
          padding-right: 5px; }
    .leaderboard.tile .leader-row .medal {
      position: absolute;
      height: 41px;
      right: 2%;
      top: -3px; }
