@keyframes animateLeftColumn {
  0% {
    transform: rotateY(180deg) translateZ(0); }
  100% {
    transform: rotateY(10deg) translateZ(-75px); } }

@keyframes animateRightColumn {
  0% {
    transform: rotateY(-180deg) translateZ(0); }
  100% {
    transform: rotateY(-10deg) translateZ(-75px); } }

.dashboard .row {
  margin-left: 15px;
  margin-right: 15px;
  perspective: 1500px; }
  .dashboard .row .left-column {
    animation: animateLeftColumn 1s ease-in 10s 1 normal forwards; }
  .dashboard .row .right-column {
    animation: animateRightColumn 1s ease-in 10s 1 normal forwards; }
  .dashboard .row .center-column {
    position: relative; }
