.photo-grid .dashboard-content {
  width: 1560px; }

.photo-grid .grid {
  display: flex;
  flex-direction: column;
  align-content: space-around; }
  .photo-grid .grid .grid-row {
    width: inherit;
    display: flex;
    align-content: space-around; }
    .photo-grid .grid .grid-row .photo-tile {
      display: block;
      margin: 10px;
      background: white;
      -webkit-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.3);
      -moz-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.3);
      box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.3); }
    .photo-grid .grid .grid-row .photo-tile-rotateX-appear {
      transform: rotateX(90deg); }
    .photo-grid .grid .grid-row .photo-tile-rotateX-appear.photo-tile-rotateX-appear-active {
      transform: rotateX(0deg);
      transition: all .8s ease-in; }
    .photo-grid .grid .grid-row .photo-tile-rotateY-appear {
      transform: rotateY(90deg); }
    .photo-grid .grid .grid-row .photo-tile-rotateY-appear.photo-tile-rotateY-appear-active {
      transform: rotateY(0deg);
      transition: all .8s ease-in; }
