@keyframes card-shine {
  0% {
    left: -850px; }
  15% {
    left: 850px; }
  100% {
    left: 850px; } }

.carousel-container {
  overflow: hidden; }
  .carousel-container .dashboard-content {
    perspective: 1000px;
    perspective-origin: center;
    width: 100vw;
    height: 100vh; }
    .carousel-container .dashboard-content .carousel {
      transform-style: preserve-3d;
      -moz-transition: transform 3s;
      -ms-transition: transform 3s;
      -webkit-transition: transform 3s;
      -o-transition: transform 3s;
      transition: transform 3s;
      position: absolute;
      width: 1040px;
      height: 700px;
      top: 50%;
      left: 50%; }
      .carousel-container .dashboard-content .carousel .tweet-card {
        position: absolute;
        display: block;
        backface-visibility: hidden;
        -moz-transition: all 3s;
        -ms-transition: all 3s;
        -webkit-transition: all 3s;
        -o-transition: all 3s;
        transition: all 3s; }
    .carousel-container .dashboard-content .tweet-card-middle.text {
      font-size: 40px; }
    .carousel-container .dashboard-content .tweet-card-middle.portrait {
      font-size: 35px; }
    .carousel-container .dashboard-content .tweet-card-middle.landscape {
      font-size: 25px; }
    .carousel-container .dashboard-content .tweet-card.focus {
      opacity: 1; }
    .carousel-container .dashboard-content .tweet-card.outfocus {
      opacity: 0.5; }

.emoji {
  width: 40px;
  height: 40px;
  padding: 0 5px; }

.tweet-card {
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  border-radius: 10px;
  background-color: #FFF;
  padding: 25px;
  padding-bottom: 0;
  margin: 15px;
  margin-bottom: 0;
  width: 1020px;
  height: 700px;
  box-sizing: border-box;
  font-family: CiscoSans;
  overflow: hidden; }
  .tweet-card.shine:before {
    content: "";
    position: absolute;
    top: 252px;
    left: -850px;
    background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5));
    height: 250px;
    width: 100%;
    transform: rotateZ(120deg);
    animation: card-shine 5s;
    animation-delay: 2s; }
  .tweet-card .tweet-card-top {
