    flex-direction: row; }
    .tweet-card .tweet-card-top .avatar {
      font-size: 0; }
      .tweet-card .tweet-card-top .avatar .image-obj {
        width: 90px;
        height: 90px;
        object-fit: cover; }
        .tweet-card .tweet-card-top .avatar .image-obj img {
