.root, body, .root #cover, .root #main {
  overflow: hidden; }

.root {
  font-family: sans-serif;
  background: black !important; }
  .root .coverCircles {
    border-radius: 50%;
    position: absolute;
    background: white; }
  .root .mainCircles {
    border-radius: 50%;
    position: absolute;
    display: flex;
    z-index: 0;
    background: white;
    border-radius: 50%;
    opacity: 1; }
    .root .mainCircles .image {
      display: flex;
      position: absolute; }
      .root .mainCircles .image div {
        background-size: cover !important; }
    .root .mainCircles .right {
      display: flex;
      position: absolute;
      width: 0px;
      height: 0px;
      left: 0px;
      top: 0px;
      justify-content: center;
      overflow: hidden; }
      .root .mainCircles .right .right-content {
        display: flex;
        flex-direction: column;
        padding: 0 5%;
        overflow: hidden; }
        .root .mainCircles .right .right-content .post-image {
          display: flex;
          width: 100%;
          height: 100%; }
          .root .mainCircles .right .right-content .post-image div {
            background-size: cover !important; }
        .root .mainCircles .right .right-content .post-text {
          display: flex;
          width: 100%;
          height: 100%;
          padding: 1% 0; }
    .root .mainCircles .left {
      display: flex;
      position: absolute;
      flex-direction: column;
      width: 0px;
      height: 0px;
      left: 0px;
      top: 0px;
      overflow: hidden; }
      .root .mainCircles .left .username {
        justify-content: center;
        display: flex;
        font-weight: 700; }
      .root .mainCircles .left .social-values {
        justify-content: center;
        display: flex;
        margin: auto;
        padding: 0 10%; }
        .root .mainCircles .left .social-values .likes {
          justify-content: center;
          padding: 10%;
          width: 100%;
          box-shadow: 0px 0px 1px black;
          font-weight: 700; }
        .root .mainCircles .left .social-values .comments {
          justify-content: center;
          padding: 10%;
          width: 100%;
          box-shadow: 0px 0px 1px black;
          font-weight: 700; }
