@import url(../polling/fontello.css);
.result-card {
  max-width: 512px;
  max-height: 547px;
  position: relative;
  border-radius: 12px;
  background-color: #ffffff;
  border: solid 10px white;
  overflow: hidden; }
  .result-card .card-photo {
    width: 511px;
    height: 285px;
    max-width: 511px;
    max-height: 547px; }
    .result-card .card-photo img {
      max-width: 511px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px; }
  .result-card .card-body {
    position: relative;
    height: 150px;
    background-color: rgba(255, 255, 255, 0.5); }
    .result-card .card-body .logo-bck {
      position: absolute;
      top: -43px;
      left: 15px;
      height: 50%;
      width: 100%; }
      .result-card .card-body .logo-bck .logo {
        position: absolute;
        left: 45px;
        top: 8px;
        width: 95px;
        height: 95px;
        border-radius: 100%;
        background-color: #ffffff;
        overflow: hidden; }
        .result-card .card-body .logo-bck .logo img {
          width: 100%; }
    .result-card .card-body .name {
      position: absolute;
      top: 5px;
      left: 180px;
      font-family: CiscoSans;
      font-size: 2em;
      color: #3d3d3d; }
    .result-card .card-body .handle {
      position: absolute;
      top: 40px;
      left: 180px;
      font-family: CiscoSans;
      font-size: 2em;
      color: #3d3d3d; }
    .result-card .card-body .like {
      position: absolute;
      top: 60px;
      left: 40px;
      font-family: CiscoSans;
      font-size: 2.75em; }
      .result-card .card-body .like .icon.icon-heart-filled {
        color: #d52525;
        font-size: 1.75em; }
    .result-card .card-body .rank {
      position: absolute;
      bottom: 0;
      right: 15px;
      color: #000000;
      font-family: CiscoSans;
      font-size: 3.5em; }

.results {
  margin-top: 230px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px; }
  .results .first {
    display: flex;
    margin-bottom: 15px; }
  .results .second-third {
    display: flex;
    justify-content: space-between; }
    .results .second-third .result-card {
      margin: 7px 7px; }
  .results .rest {
    display: flex; }
    .results .rest .result-card {
      margin: 7px 7px; }
      .results .rest .result-card .card-photo {
        max-width: 330px; }
        .results .rest .result-card .card-photo img {
          max-width: 330px; }
      .results .rest .result-card .card-body {
        height: 120px; }
        .results .rest .result-card .card-body .logo-bck {
          top: -35px;
          left: 15px;
          height: 50%;
          width: 100%; }
          .results .rest .result-card .card-body .logo-bck .logo {
            left: 34px;
            top: 8px;
            width: 80px;
            height: 80px;
            overflow: hidden; }
            .results .rest .result-card .card-body .logo-bck .logo img {
              width: 100%; }
        .results .rest .result-card .card-body .name {
          left: 140px;
          font-size: 1.2em; }
        .results .rest .result-card .card-body .handle {
          top: 30px;
          left: 140px;
          font-size: 0.8em; }
        .results .rest .result-card .card-body .like {
          font-size: 1.4em; }
          .results .rest .result-card .card-body .like .icon {
            font-size: 1.8em; }
        .results .rest .result-card .card-body .rank {
          font-size: 2em; }
