@import url(./fontello.css);
@keyframes scroll-up {
  0% {
    transform: translateY(0%); }
  100% {
    transform: translateY(-100%); } }

.overlay-parent {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 5; }

.overlay {
  margin-top: 50px;
  position: fixed;
  top: 100px;
  left: 100px;
  z-index: 10; }
  .overlay .heading {
    color: #ffffff;
    text-align: center;
    font-size: 3em;
    margin-bottom: 55px;
    font-family: CiscoSans; }
  .overlay .selected-card {
    border: solid 10px white;
    border-radius: 20px;
    position: relative; }
    .overlay .selected-card .likes .icon {
      position: absolute;
      display: none; }
      .overlay .selected-card .likes .icon.up {
        display: block;
        position: absolute;
        top: 25%;
        left: 40%;
        opacity: 0.8;
        font-size: 10em;
        color: #d52525; }
    .overlay .selected-card .selected-card-photo {
      max-height: 900px;
      overflow: hidden; }
      .overlay .selected-card .selected-card-photo img {
        margin-bottom: -5px;
        width: 850px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px; }
    .overlay .selected-card .selected-card-body {
      height: 200px;
      position: relative;
      background-color: #ffffff; }
      .overlay .selected-card .selected-card-body .cisco-logo-bck {
        position: absolute;
        top: -62px;
        left: 10px;
        height: 56%;
        width: 100%;
        background-repeat: no-repeat;
        background-image: url(data:image/svg+xml;base64,); }
      .overlay .selected-card .selected-card-body .cisco-logo {
        position: absolute;
        top: 10px;
        left: 72px;
        width: 125px;
        height: 125px;
        border-radius: 100%;
        background-color: #1dc0ee;
        border: 5px solid #ffffff;
        overflow: hidden; }
        .overlay .selected-card .selected-card-body .cisco-logo .author-pic {
          position: absolute;
          width: 100%; }
        .overlay .selected-card .selected-card-body .cisco-logo .default-pic {
          position: absolute;
          top: 30px;
          left: 10px;
          width: 100px; }
      .overlay .selected-card .selected-card-body .cisco-heading {
        position: absolute;
        font-family: CiscoSansThin;
        font-size: 3em;
        color: #3d3d3d;
        top: 20px;
        left: 300px;
        width: 390px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap; }
      .overlay .selected-card .selected-card-body .social-logo {
        position: absolute;
        top: 55px;
        right: 55px;
        font-size: 4em; }
        .overlay .selected-card .selected-card-body .social-logo img {
          width: 80px;
          height: 80px; }
        .overlay .selected-card .selected-card-body .social-logo .icon {
          background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); }
      .overlay .selected-card .selected-card-body .current {
        font-family: CiscoSansThin;
        font-size: 2em; }
        .overlay .selected-card .selected-card-body .current.down-vote {
          position: absolute;
          top: 120px;
          left: 90px; }
          .overlay .selected-card .selected-card-body .current.down-vote .icon {
            color: #777777;
            font-size: 3em; }
        .overlay .selected-card .selected-card-body .current.up-vote {
          position: absolute;
          top: 105px;
          left: 50px; }
          .overlay .selected-card .selected-card-body .current.up-vote .icon {
            color: #d52525;
            font-size: 2.5em; }
  .overlay .vote {
    display: flex;
    justify-content: space-around; }
    .overlay .vote .vote-action {
      margin-top: 60px;
      position: relative;
      width: 222px;
      height: 168px;
      background: #ffffff;
      padding: 10px;
      border-radius: 40%;
      border: 10px solid #d5d5d5; }
      .overlay .vote .vote-action.down.blink {
        pointer-events: none;
        cursor: default; }
        .overlay .vote .vote-action.down.blink img {
          top: 0;
          transform: scaleX(-1) rotate(180deg); }
      .overlay .vote .vote-action.down img {
        position: absolute;
        top: 45px;
        left: 40px; }
      .overlay .vote .vote-action.up.blink {
        pointer-events: none;
        cursor: default; }
        .overlay .vote .vote-action.up.blink img {
          bottom: 0px;
          transform: scaleX(-1) rotate(0deg); }
        .overlay .vote .vote-action.up.blink .icon {
          color: #d52525; }
      .overlay .vote .vote-action.up .icon {
        text-shadow: 0 0 10px grey; }
      .overlay .vote .vote-action.up img {
        transform: rotate(180deg);
        position: absolute;
        bottom: 45px;
        left: 40px; }
      .overlay .vote .vote-action .icon {
        position: absolute;
        font-size: 5em;
        color: #ffffff;
        top: 50px;
        left: 70px; }
  .overlay .close {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px; }
    .overlay .close .icon {
      font-size: 4em;
      color: #ffffff; }
    .overlay .close div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 130px;
      height: 130px;
      border: 5px solid #ffffff;
      background-color: #3d3d3d;
      border-radius: 100%; }

.parent {
  display: flex;
  overflow: hidden;
  height: 85vh;
  margin-top: 125px; }

.initial {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap; }
  .initial .col.pause {
    animation-play-state: paused !important; }
  .initial .col .card .card-body {
    border: solid 4px white; }
    .initial .col .card .card-body .photo {
      width: 520px;
      position: relative; }
      .initial .col .card .card-body .photo .media {
        width: 520px; }
      .initial .col .card .card-body .photo .logo {
        width: 50px;
        position: absolute;
        bottom: 30px;
        right: 20px; }
        .initial .col .card .card-body .photo .logo.instagram {
          border-radius: 17px; }
