.stats .background {
  opacity: 0.75;
  position: relative;
  height: 100vh; }
  .stats .background div.image {
    position: absolute;
    background-size: cover; }

.stats .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden; }
  .stats .content .group {
    position: absolute;
    width: 100%;
    top: 0; }
  .stats .content .box {
    display: inline-block;
    width: 400px;
    height: 400px;
    text-align: center;
    border-radius: 20px;
    margin: calc(50vh - 150px) 2vw 0 2vw;
    position: relative;
    vertical-align: top;
    transition: transform 1s ease-in-out;
    transform: scale(0); }
    .stats .content .box.show {
      transform: scale(1); }
    .stats .content .box .circle {
      box-sizing: border-box;
      display: inline-block;
      border-radius: 50%;
      box-shadow: 10px 10px 25px rgba(0, 0, 0, 0.3);
      width: 100%;
      height: 100%;
      background-color: #0c3c56;
      padding: 25px;
      border: 48px solid #176892;
      color: white; }
      .stats .content .box .circle .icon {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        width: 50px;
        height: 50px;
        margin: auto;
        display: inline-block; }
      .stats .content .box .circle .count {
        font-size: 76px;
        font-family: CiscoSans;
        line-height: 90px;
        margin-top: 5px; }
      .stats .content .box .circle .label {
        font-family: CiscoSans;
        font-size: 32px;
        border-top: 4px solid rgba(255, 255, 255, 0.4);
        padding-top: 10px; }
    .stats .content .box svg {
      position: absolute;
      transform: rotate(-90deg);
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      fill: none; }
      .stats .content .box svg circle {
        stroke: #24b0e8;
        stroke-width: 50px;
        stroke-linecap: round;
        transition: all 5s ease-out; }
    .stats .content .box .data {
      font-size: 32px;
      text-align: center; }
  .stats .content .top-tweets {
    overflow: hidden;
    height: 100vh;
    perspective: 1000px; }
    .stats .content .top-tweets .subheading {
      margin-top: 16vh;
      color: #005073;
      font-size: 32px;
      line-height: 32px;
      font-family: CiscoSans; }
    .stats .content .top-tweets .card {
      position: absolute;
      top: 20vh;
      left: 0;
      width: 100vw;
      height: 100vh;
      transform-style: preserve-3d;
      transition: all 1s;
      transform: scale(0) rotateY(90deg); }
      .stats .content .top-tweets .card.show {
        transition-delay: 1s;
        transform: scale(1) rotateY(0); }
      .stats .content .top-tweets .card .tweet-card {
        display: inline-block;
        transform: scale(0.8); }
        .stats .content .top-tweets .card .tweet-card .tweet-card-middle .text-content {
          font-size: 32px; }
      @media (max-height: 800px) {
        .stats .content .top-tweets .card {
          top: 0; }
          .stats .content .top-tweets .card .tweet-card {
            transform: scale(0.6); } }
