@keyframes scrollphotos {
  0% {
    transform: translate(0, 0); }
  99.9% {
    transform: translate(0, -90%); }
  100% {
    transform: translate(0, 0); } }

.photogridtile {
  height: 100%;
  box-sizing: border-box; }
  .photogridtile .body {
    overflow: hidden; }
    .photogridtile .body .photos {
      font-size: 0;
      width: 50%;
      float: left;
      height: 10000px;
      animation: scrollphotos 180s linear infinite; }
      .photogridtile .body .photos img {
        break-inside: avoid;
        padding: 5px;
        width: 100%;
        box-sizing: border-box; }
      .photogridtile .body .photos img.hide {
        transform: rotateY(360deg) scale(0);
        transition: all 2s ease;
        width: 0; }
