.horizontal-stream {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1080px;
  height: 10000px;
  overflow-y: hidden;
  z-index: 1; }
  .horizontal-stream .tweet-card {
    height: auto;
    padding: 25px 25px 0 25px; }
    .horizontal-stream .tweet-card .tweet-card-middle {
      font-size: 35px; }
    .horizontal-stream .tweet-card .tweet-card-middle.portrait, .horizontal-stream .tweet-card .tweet-card-middle.landscape {
      min-height: auto;
      height: auto; }
      .horizontal-stream .tweet-card .tweet-card-middle.portrait .emoji, .horizontal-stream .tweet-card .tweet-card-middle.landscape .emoji {
        width: 35px;
        height: 35px; }
      .horizontal-stream .tweet-card .tweet-card-middle.portrait .image img, .horizontal-stream .tweet-card .tweet-card-middle.landscape .image img {
        height: auto;
        object-fit: initial; }
    .horizontal-stream .tweet-card .tweet-card-middle.text {
      min-height: auto;
      height: auto; }
      .horizontal-stream .tweet-card .tweet-card-middle.text .text-content {
        padding-top: 20px; }
  .horizontal-stream .tweet-column-wrapper {
    overflow-y: hidden; }
  .horizontal-stream .tweet-column.slide {
    transform: translateY(0);
    transition: transform 1s linear; }
