.ticker {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 110px;
  padding: 20px 0;
  white-space: nowrap;
  background: #005073;
  box-sizing: border-box; }
  .ticker .tweet {
    display: inline-block;
    white-space: nowrap;
    position: absolute;
    transition-property: left;
    transition-timing-function: linear;
    box-sizing: border-box; }
    .ticker .tweet .tweet-content {
      display: flex;
      align-items: center; }
      .ticker .tweet .tweet-content .avatar {
        width: 60px;
        height: 65px;
        padding-left: 25px; }
        .ticker .tweet .tweet-content .avatar img, .ticker .tweet .tweet-content .avatar object {
          border: 1px solid #DDD;
          width: 64px;
          height: 64px; }
      .ticker .tweet .tweet-content .handle, .ticker .tweet .tweet-content .text, .ticker .tweet .tweet-content .timeago {
        font-family: CiscoSansBold;
        font-weight: bold; }
      .ticker .tweet .tweet-content .handle, .ticker .tweet .tweet-content .text, .ticker .tweet .tweet-content .timeago {
        color: #FFF; }
      .ticker .tweet .tweet-content .handle {
        font-size: 34px;
        font-family: CiscoSansBold;
        padding-left: 35px; }
      .ticker .tweet .tweet-content .text {
        padding-left: 32px;
        font-size: 31px;
        font-family: CiscoSans; }
      .ticker .tweet .tweet-content .timeago {
        padding-left: 37px;
        font-size: 19px; }
      .ticker .tweet .tweet-content .twitter-bird {
        height: 64px;
        padding-left: 25px; }
