@-webkit-keyframes marquee {
  0% {
    transform: translate(0, 0); }
  100% {
    transform: translate(-100%, 0); } }

@keyframes marquee {
  0% {
    transform: translate(0, 0); }
  100% {
    transform: translate(-100%, 0); } }

.ask-fullscreen {
  display: flex;
  align-items: center;
  justify-content: center; }
  .ask-fullscreen button {
    margin-left: 10px;
    margin-top: 50%;
    border-radius: 3px;
    background-color: #1890ff;
    padding: 7px 20px;
    color: white;
    outline: 0;
    border-style: none; }

.marquee-container-qr .left, .marquee-container-qr .right {
  background: linear-gradient(to top, #ffffff, #dcdcdc, #ffffff); }

.marquee-container-qr .left {
  position: fixed;
  width: 80%;
  height: 120px;
  bottom: 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  white-space: nowrap; }
  .marquee-container-qr .left .tweet {
    display: inline-block;
    border-right: 3px solid #9c9c9c;
    border-radius: 3px; }
    .marquee-container-qr .left .tweet .tweet-content {
