      align-items: center;
      margin-right: 40px; }
      .marquee-container-qr .left .tweet .tweet-content .medal {
        display: none;
        background-repeat: no-repeat;
        background-size: contain;
        height: 31px;
        width: 25px;
        position: relative;
        top: -10px;
        left: 20px; }
        .marquee-container-qr .left .tweet .tweet-content .medal.medal-0 {
          display: block;
          background-image: url("/assets/medals/gold.png"); }
        .marquee-container-qr .left .tweet .tweet-content .medal.medal-1 {
          display: block;
          background-image: url("/assets/medals/silver.png"); }
        .marquee-container-qr .left .tweet .tweet-content .medal.medal-2 {
          display: block;
          background-image: url("/assets/medals/bronze.png"); }
      .marquee-container-qr .left .tweet .tweet-content .avatar {
        width: 50px;
        height: 50px;
        padding-left: 25px; }
        .marquee-container-qr .left .tweet .tweet-content .avatar img, .marquee-container-qr .left .tweet .tweet-content .avatar object {
          border: 1px solid #DDD;
          width: 50px;
          height: 50px;
          border-radius: 100px; }
      .marquee-container-qr .left .tweet .tweet-content .handle, .marquee-container-qr .left .tweet .tweet-content .text, .marquee-container-qr .left .tweet .tweet-content .timeago {
        font-family: Roboto;
        font-weight: bold; }
      .marquee-container-qr .left .tweet .tweet-content .handle, .marquee-container-qr .left .tweet .tweet-content .text {
        color: #464646; }
      .marquee-container-qr .left .tweet .tweet-content .handle {
        font-size: 1.3em;
        font-family: Roboto;
        padding-left: 35px;
        padding-top: 1em;
        color: #373737; }
      .marquee-container-qr .left .tweet .tweet-content .image img {
        padding-left: 20px;
        width: 70px;
        height: 70px; }
      .marquee-container-qr .left .tweet .tweet-content .text {
        padding-left: 32px;
        font-size: 31px; }
        .marquee-container-qr .left .tweet .tweet-content .text .emoji {
          width: 28px;
          height: 28px;
          padding: 0; }
      .marquee-container-qr .left .tweet .tweet-content .timeago {
