.marquee-container-qr .right {
  width: 21%;
  height: 120px;
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex; }
  .marquee-container-qr .right img {
    margin: 0 15px;
    height: 51px;
    margin: auto; }
