.mediacarouseltile {
  background-color: transparent; }
  .mediacarouseltile .body {
    height: 1040px;
    position: relative; }
  .mediacarouseltile .wrapper {
    position: absolute;
    top: 5px;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0px;
    box-sizing: border-box;
    opacity: 1; }
    .mediacarouseltile .wrapper.hide .card-container {
      transform: scale(0);
      opacity: 0; }
    .mediacarouseltile .wrapper .card-container {
      margin-bottom: 20px; }
      .mediacarouseltile .wrapper .card-container.top {
        transition: transform 1.5s ease-out, opacity 1s ease-out; }
      .mediacarouseltile .wrapper .card-container.bottom {
        transition: transform 1.5s ease-out, opacity 1s ease-out;
        transition-delay: 4s; }
    .mediacarouseltile .wrapper .tweet-card {
      position: relative;
      width: 100%;
      height: 410px;
      padding: 10px;
      margin: 0;
      background-color: white;
      display: flex;
      flex-direction: column; }
      .mediacarouseltile .wrapper .tweet-card .tweet-card-top .avatar .image-obj {
        width: 70px;
        height: 70px; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-top .avatar .image-obj img {
          width: 70px;
          height: 70px; }
      .mediacarouseltile .wrapper .tweet-card .tweet-card-top .profile {
        overflow: hidden; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-top .profile .name {
          font-size: 32px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-top .profile .handle {
          font-size: 22px; }
      .mediacarouseltile .wrapper .tweet-card .tweet-card-top .timestamp {
        font-size: 15px;
        padding-right: 10px;
        white-space: nowrap; }
      .mediacarouseltile .wrapper .tweet-card .tweet-card-middle {
        padding: 10px 0;
        min-height: 350px;
        font-size: 1.25rem;
        flex-grow: 1; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.portrait {
          height: auto;
          min-height: 0;
          display: block; }
          .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.portrait .image {
            float: left;
            width: 300px;
            height: 250px;
            overflow: hidden; }
            .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.portrait .image img {
              width: 300px;
              height: auto; }
          .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.portrait .text-content {
            overflow: hidden; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.landscape {
          height: auto;
          min-height: 0;
          display: flex;
          flex-direction: column; }
          .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.landscape .image {
            padding-left: 97px;
            flex-grow: 0; }
            .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.landscape .image img {
              height: 165px;
              width: auto; }
          .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.landscape .text-content {
            padding-left: 97px;
            overflow: hidden;
            flex-grow: 1; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.text {
          height: auto;
          min-height: 250px; }
          .mediacarouseltile .wrapper .tweet-card .tweet-card-middle.text .text-content {
            padding-left: 97px; }
      .mediacarouseltile .wrapper .tweet-card .tweet-card-bottom {
        border-top: none;
        padding: 0 10px; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-bottom .metrics .shares, .mediacarouseltile .wrapper .tweet-card .tweet-card-bottom .metrics .likes {
          color: #7D7D7D; }
        .mediacarouseltile .wrapper .tweet-card .tweet-card-bottom .metrics img {
          height: 20px;
          padding: 4px 0;
          width: auto; }
